body {
  margin: 0; padding: 0; zoom: 1;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Bungee', cursive;
}

/*
span.bungee { font-family: 'Bungee', cursive; }*/

.app {
  max-width: 720px;
  margin: 0 auto;
  text-align: center;
}

.header { font-size: 60px; font-family: 'Oswald', sans-serif; letter-spacing: 2px; font-weight:700; color: #d8d8d8;}
.header:hover {
  color: #99C24D;
  /*color: #E26D5C;*/
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-animation: egg 5s infinite;
          animation: egg 5s infinite;
}

@-webkit-keyframes egg {
  0% {
    color: #99C24D;
  }
  50% {
    color: #41BBD9;
  }
  100% {
    color: #99C24D;
  }
}

@keyframes egg {
  0% {
    color: #99C24D;
  }
  50% {
    color: #41BBD9;
  }
  100% {
    color: #99C24D;
  }
}

.subheader { font-size: 20px; font-family: 'Oswald', sans-serif; color: #FBB13C; }

a.link {
  text-decoration: none;
  color: #56A3A6;
  font-family: Oswald;
  font-weight:700;
}

a.link:hover {
  text-decoration: none;
  border-bottom: 2px solid #000;
  color: #99C24D;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.stock-container {
  display: inline-block;
  padding: 20px;
  padding-left: 10px;
  width: 130px;
  font-family: 'Nunito', sans-serif;
  font-weight:700;
}

.select-container span {
  font-family: 'Nunito', sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #d8d8d8;
}

.select-container span:hover {
  border-bottom: 2px solid #000;
  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.select-container select {
  padding: 10px;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: transparent;
  background-image: none;
  -webkit-appearance: none;
  font-size: 18px;
  font-family: 'Oswald', sans-serif; font-weight:700;
  cursor: pointer;
  color: #FBB13C;
}

.select-container select:hover,
.select-container select:focus {
  outline: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

#right  {
  color: #43adad;
}

#left {
  color: #ff494f;
}

.select-container select:focus {
  outline: none;
}


